<template>
  <div class="sales-board">
      <div class="sales-board-intro">
        <h2></h2>
        <p></p>
      </div>
      <div class="sales-board-form">
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  <v-count :max="buyNumber.max" :min="buyNumber.min"></v-count>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  <v-mulchoose :selections="versionList"></v-mulchoose>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  500 
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">&nbsp;</div>
              <div class="sales-board-line-right">
                  <div class="button">
                    
                  </div>
              </div>
          </div>
      </div>
      <div class="sales-board-des">
        <h2></h2>
        <p>2020



</p>
      </div>
  </div>
</template>

<script>
import vMulchoose from '@/components/MulChooser'
import vCount from '@/components/Counter'
export default {
  name: 'forecast',
  data () {
    return {
      versionList: [
        {
          label: '',
          value: 0
        },
        {
          label: 'pdf',
          value: 1
        },
        {
          label: '',
          value: 2
        },
        {
          label: '',
          value: 3
        }
      ],
      buyNumber:{
          max:20,
          min:1
      }
    }
  },
  components:{
      vMulchoose,
      vCount
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



